.cube-animation
  display: flex
  justify-content: center
  align-items: center
  height: 100vh
  background: #050505
  &__cube
    position: relative
    width: $size
    height: $size
    transform-style: preserve-3d
    transform: rotateX(-30deg)
    animation: animate 4s linear infinite
    div
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
      transform-style: preserve-3d
      span
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: linear-gradient(#151515, #00ec00)
        transform: rotateY(calc(90deg * var(--i))) translateZ(calc( $size / 2 ))
  &__top
    position: absolute
    top: 0
    left: 0
    width: $size
    height: $size
    background: #222
    transform: rotateX(90deg) translateZ(calc( $size / 2 ))
    &::before
      content: ''
      position: absolute
      top: 0
      left: 0
      width: $size
      height: $size
      background: #0f0
      transform: translateZ(-380px)
      filter: blur(20px)
      box-shadow: 0 0 120px rgba(0, 255,0,0.2 ), 0 0 200px rgba(0,255,0,0.4 ), 0 0 300px rgba(0,255,0,0.6 ), 0 0 400px rgba(0,255,0,0.8 ), 0 0 500px rgba(0,255,0,1 )
